<template>
  <div>
    <div
      v-if="$route.query.selected == 2"
      class="bg_back"
    >
      <div class="p12 g_f22 g_fw">{{ add_totalPrice | point }}</div>
      <div>收款总额（元）</div>
    </div>
    <div
      v-if="currentInfo.length > 0"
      id="moneyDetailsBox"
      :style="{top:$route.query.selected == 2?'135px':'50px'}"
    >
      <div
        v-for="(item,i) in currentInfo"
        :key="i"
        @click="btn_go(item)"
      >
        <div
          class="day"
          v-if="$route.query.selected == 1"
        >
          <div class="per_info">
            <p class="c_000">
              {{ item.orderRoom.sysUser!=null?item.orderRoom.sysUser.realName?item.orderRoom.sysUser.realName:item.orderRoom.order.roomUser:item.orderRoom.order.roomUser }}
              <span>{{ item.orderRoom.sysUser!=null?item.orderRoom.sysUser.phone:item.orderRoom.order.phone }}</span>
            </p>
            <p>{{ orderSourceMap[item.orderRoom.order.orderSource] }}</p>
          </div>
          <div class="day_bottom">
            <div class="scalename">
              <p>{{ item.orderRoom.roomScale.scaleName }}<br>
              <div>共1间 房间号{{ item.orderRoom.roomSn }}</div>
              <div class="c_000">{{ item.orderRoom.order.startTime | dataFilter('MM/DD') }}至{{ item.orderRoom.order.endTime | dataFilter('MM/DD') }}</div>
            </div>
          </div>
          <div class="money">
            <p class="c_000">
              结算金额：
              <span class="c_ff">&yen;{{ item.balanceAmount }}</span>
              <span
                v-if="payment"
                class="f12"
                :style="item.payment != 0?{'color':'green'}:{'color':'red'}"
              >({{item.payment != 0?payment[item.payment]:'未支付'}})</span>
            </p>
            <p
              v-if="$route.query.platNum == '线下收款'"
              class="c_000"
            >
              收款账户：
              <span class="c_ff">{{ item.payeeAccount }}</span>
            </p>
            <p
              class="c_000"
              v-if="item.depositAmount > 0"
            >
              扣押金额：
              <span class="c_ff">&yen;{{ item.depositAmount }} </span>
              <span
                class="f12"
                :style="item.depositPayment != 0?{'color':'green'}:{'color':'red'}"
              > ({{payment[item.depositPayment]}})</span>
            </p>
          </div>
          <div
            class="money"
            v-if="item.reverseAmount >0"
          >
            <p class="c_000">
              冲账：
              <span class="c_ff">-&yen;{{ item.reverseAmount }}</span>
            </p>
          </div>
          <div class="money c_000">订单号：{{ item.orderRoom.order.orderSn }}</div>
        </div>
        <div
          v-else
          class="long"
        >
          <p>
            <span>{{ item.startTime | dataFilter('YYYY-MM-DD') }}至{{ item.endTime | dataFilter('YYYY-MM-DD') }} <span
                class="type_style"
                :class="item.monthBillType == '1' || item.monthBillType == '3'?'type1':'type2'"
              >{{ monthBillTypeObj[item.monthBillType] }}</span> </span>
            <span>&yen;{{ item.totalPrice | point }}</span>
          </p>
          <div class="g_c_b2">到账时间：{{ item.billTime }}</div>
          <div class="g_c_b2 g_bt">{{ item.orderRoom.roomSn }} {{ item.roomUser }}</div>
        </div>
      </div>
    </div>
    <h2 v-else>暂无数据</h2>
  </div>
</template>
<script>
import '@/assets/styles/global.css';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessBillRecordListByType, GetBusinessBillRecordList, GetBusinessMonthBillRecordList, GetBusinessRentalBillByorderroom, GetBusinessLifeCostBillByorderroom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            currentInfo: [],
            orderSourceMap: {},
            payment: null,
            paySource: null,
            add_totalPrice: 0,
            monthBillTypeObj: { 1: '房费', 2: '生活', 3: '电费', 4: '押金F' }
        };
    },
    created() {
        this.$store.commit('setHeader', '结算订单');
        this.getInfo(this.$route.query.billRecordId);
    },
    methods: {
        getInfo(id) {
            // 日租
            if (this.$route.query.selected == 1) {
                GetBusinessBillRecordList(id).done((res) => {
                    if (res.data.length > 0) {
                        this.dealList(res.data);
                    }
                });
            } else {
                // 长租
                GetBusinessMonthBillRecordList(id).done((res) => {
                    this.currentInfo = res.data;
                    this.add_totalPrice = 0;
                    res.data.forEach((cValue) => {
                        this.add_totalPrice += cValue.totalPrice;
                    });
                    this.getDay();
                });
            }
        },
        // 过滤
        dealList(list) {
            if (this.$route.query.platNum == '2-1') {
                //结算合计
                this.currentInfo = list.filter((item) => item.depositAmount > 0 && 0 < item.depositPayment && item.depositPayment < 4);
            } else if (this.$route.query.platNum == '2-2') {
                //微信结算
                this.currentInfo = list.filter((item) => item.depositAmount > 0 && 0 < item.depositPayment && item.depositPayment < 3);
            } else if (this.$route.query.platNum == '2-3') {
                //支付宝结算
                this.currentInfo = list.filter((item) => item.depositAmount > 0 && item.depositPayment == 3);
            } else if (this.$route.query.platNum == '3-1') {
                //哎哟有房
                this.currentInfo = list.filter((item) => item.payment > 0 && item.payment < 4);
            } else if (this.$route.query.platNum == '3-2') {
                this.currentInfo = list.filter((item) => item.billPlat == 3);
                //携程
            } else if (this.$route.query.platNum == '3-3') {
                //美团
                this.currentInfo = list.filter((item) => item.billPlat == 2);
            } else if (this.$route.query.platNum == '3-4') {
                //飞猪
                this.currentInfo = list.filter((item) => item.billPlat == 4);
            } else if (this.$route.query.platNum == '3-5') {
                //其他平台
                this.currentInfo = list.filter((item) => item.billPlat == 9);
            } else if (this.$route.query.platNum == '线下收款') {
                //线下收款
                this.currentInfo = list.filter((item) => item.billPlat == 5 && item.payment > 4);
            } else if (this.$route.query.platNum == '4-1') {
                //现金
                this.currentInfo = list.filter((item) => item.billPlat > 1);
            } else if (this.$route.query.platNum == '4-2') {
                //微信
                this.currentInfo = list.filter((item) => item.billPlat > 1);
            } else if (this.$route.query.platNum == '4-3') {
                //支付宝
                this.currentInfo = list.filter((item) => item.billPlat > 1);
            } else if (this.$route.query.platNum == '4-4') {
                //银行卡
                this.currentInfo = list.filter((item) => item.billPlat > 1);
            } else if (this.$route.query.platNum == '4-5') {
                //对公转账
                this.currentInfo = list.filter((item) => item.billPlat > 1);
            } else if (this.$route.query.platNum == '5-1') {
                // 延时退房扣押
                this.getBillRecord({ type: 'autoDepositCutPay' });
            } else if (this.$route.query.platNum == '5-2') {
                // 查房扣押
                this.getBillRecord({ type: 'depositCutPay' });
            } else if (this.$route.query.platNum == '5-3') {
                // 提前退房补差
                this.currentInfo = list.filter((item) => item.extraAmount > 0);
            } else if (this.$route.query.platNum == '6-1') {
                // 冲账
                this.currentInfo = list.filter((item) => item.reverseAmount > 0);
            } else if (this.$route.query.platNum == '6-2') {
                // 退款
                this.currentInfo = list.filter((item) => item.refuseAmount > 0);
            }
            if (this.currentInfo.length > 0 && this.$route.query.selected == 1) {
                this.getDay();
            } else if (this.currentInfo.length > 0 && this.$route.query.selected == 2) {
                this.getMonth();
            }
        },
        // 根据类型查询账单明细-延时退房扣押/查房扣押
        getBillRecord(obj) {
            let data = {
                billRecordId: this.$route.query.billRecordId,
                type: obj.type
            };
            GetBusinessBillRecordListByType(data).done((res) => {
                this.currentInfo = res.data;
                if (this.currentInfo.length > 0 && this.$route.query.selected == 1) {
                    this.getDay();
                } else if (this.currentInfo.length > 0 && this.$route.query.selected == 2) {
                    this.getMonth();
                }
            });
        },
        // 支付方式
        getDay() {
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let obj = {};
                for (var i = 0; i < res.data.length; i++) {
                    obj[res.data[i].dictValue] = res.data[i].dictLabel;
                }
                this.orderSourceMap = obj;
            });
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                let payment = {};
                res.data.forEach((item) => {
                    payment[item.dictValue] = item.dictLabel;
                });
                this.payment = payment;
            });
        },
        // 账单来源
        getMonth() {
            GetSystemDictDataByType({ dictType: 'business_month_bill_type' }).done((res) => {
                let payment = {};
                res.data.forEach((item) => {
                    payment[item.dictValue] = item.dictLabel;
                });
                this.paySource = payment;
            });
        },
        // 跳转详情
        btn_go(item) {
            // 跳转-房费账单
            if (item.monthBillType == 1) {
                this.getRoom(item);
                setTimeout(() => {
                    this.$router.push({
                        path: '/roomFeeD',
                        query: {
                            rentalBillId: this.RoomList.id
                        }
                    });
                }, 1000);
            } else if (item.monthBillType == 2) {
                this.getLife(item);
                // 跳转-生活账单
                setTimeout(() => {
                    this.$router.push({
                        path: '/lifeFeeD',
                        query: {
                            life_fee: this.LifeList
                        }
                    });
                }, 1000);
            }
        },
        // 长租账单信息-房费
        getRoom(item) {
            GetBusinessRentalBillByorderroom(item.orderRoomId).done((res) => {
                this.RoomList = res.data[0];
            });
        },
        // 长租账单信息-生活账单
        getLife(item) {
            GetBusinessLifeCostBillByorderroom(item.orderRoomId).done((res) => {
                this.LifeList = result.data[0];
            });
        }
    }
};
</script>
<style scoped>
#moneyDetailsBox {
    font-size: 14px;
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
}
.day {
    padding: 0 10px;
    background-color: #fff;
    margin-bottom: 15px;
    margin: 8px 10px;
    border-radius: 5px;
}
.day > .day_bottom {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #f6f6f6;
    align-items: center;
}
.day > .day_bottom > .scalename {
    text-align: left;
    line-height: 30px;
    color: #707070;
}
.day > .day_bottom > .scalename > p:nth-child(1) {
    margin-top: 8px;
}
.day > .money {
    padding: 5px 0;
    text-align: left;
    line-height: 1.2rem;
}
.long {
    margin: 8px 10px;
    background: #fff;
    border-radius: 5px;
    text-align: left;
    padding: 0 10px;
    line-height: 35px;
}
.long > p {
    display: flex;
    justify-content: space-between;
}
.type_style {
    font-size: 12px;
    padding: 0px 4px;
    margin-left: 8px;
    border-radius: 2px;
}
.bg_back {
    font-size: 14px;
    background: #000;
    color: #fff;
    height: 80px;
}
.type1 {
    color: #ff7d41;
    border: 1px solid #ff7d41;
}
.type2 {
    color: #02bc6c;
    border: 1px solid #02bc6c;
}
.p12 {
    padding: 12px 0 5px;
}
.c_000 {
    color: #000;
}
.c_ff {
    color: #ff7d41;
}
.f12 {
    font-size: 0.7rem;
}
</style>



